  static get styles() {
    return [
      styles,
      css`
      #welcomeBar {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
      }

      #welcomeCard,
      #infoCard {
        padding: 15px;
        padding-top: 0px;
      }

      sl-card::part(footer) {
        display: flex;
        justify-content: flex-end;
      }

      #inputGroup {
        display: flex;
        background-color: #ffffff;
        color: #ffffff;
        flex-direction: row;
        border: 1px solid #ccc;
        padding: 8px 12px;
        border-radius: 8px;
        margin-top: 15px;

     }

     input {
          flex-grow: 2;
          border: none;
          font-size: 16px;
      }

      input:focus {
          outline: none;
      }

      @media(min-width: 750px) {
        sl-card {
          width: 100vw;
        }
      }


      @media (horizontal-viewport-segments: 2) {
        #welcomeBar {
          flex-direction: row;
          align-items: flex-start;
          justify-content: space-between;
        }

        #welcomeCard {
          margin-right: 20px;
        }
      }
    `];
  }

